import styled, { RuleSet, css } from "styled-components";

/*--------------------------
    Variables
--------------------------*/

const TopbarZindex = 1000; // topbar z
const TopbarHeight = 70; // topbar 
const FooterHeight = 50; // footer 
const PhoneMaxWidth = 599; //  
const TabletMaxWidth = 899; //  
const ModalDefaultZindex = 2000; // modal  z

/*--------------------------
    Style Component
--------------------------*/
const FlexBreak = styled.div`
    flex-basis: 100%;
    height: 0;
`;
const BaseStyle = css`
    display: flex;
    border: none;
    align-items: center;
    justify-content: center;
    transition: opacity 0.3s;
    cursor: pointer;
`;

//  disabled
const BaseButton = styled.button.attrs<{ disabled?: boolean }>({
    type: "button",
})`
    ${BaseStyle}

    ${(props) => {
        if (props.disabled) {
            return css`
                cursor: default;
                opacity: 0.5;
            `;
        } else {
            return css`
                ${getPCMedia(css`
                    &:hover {
                        opacity: 0.8;
                    }
                `)}
            `;
        }
    }}
`;

/*--------------------------
	Methods
--------------------------*/

/**
 * PC media css code
 * @param cssText
 * @returns css
 */
const getPCMedia = (cssText: RuleSet<object>): RuleSet<object> => {
    return css`
        @media (min-width: ${TabletMaxWidth + 1}px) {
            ${cssText}
        }
    `;
};

/**
 *  media css code
 * @param cssText
 * @returns css
 */
const getTabletMedia = (cssText: RuleSet<object>): RuleSet<object> => {
    return css`
        @media (max-width: ${TabletMaxWidth}px) and (min-width: ${PhoneMaxWidth + 1}px) {
            ${cssText}
        }
    `;
};

/**
 *  media css code
 * @param cssText
 * @returns css
 */
const getPhoneMedia = (cssText: RuleSet<object>): RuleSet<object> => {
    return css`
        @media (max-width: ${PhoneMaxWidth}px) {
            ${cssText}
        }
    `;
};

/**
 * 12 (12px)
 * @param size
 * @returns css
 */
const getSmallFont = (size: number): RuleSet<object> => {
    if (size >= 12) {
        return css`
            font-size: ${size}px;
        `;
    }

    const rate = size / 12;

    return css`
        font-size: 12px;
        zoom: ${rate};
    `;
};

/**
 * ...css
 * @param size
 * @returns css
 */
const getMultLineEllipsis = (line: number): RuleSet<object> => {
    return css`
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: normal;
        -webkit-line-clamp: ${line};
        -webkit-box-orient: vertical;
    `;
};

/*--------------------------
    Export
--------------------------*/
const GlobalStyle = {
    /*--------------------------
		Variables
	--------------------------*/
    TopbarZindex, // topbar z
    TopbarHeight, // topbar 
    FooterHeight, // footer 
    PhoneMaxWidth, //  
    TabletMaxWidth, //  
    ModalDefaultZindex, // modal  z

    /*--------------------------
		Component
	--------------------------*/
    FlexBreak, // flex-wrap
    BaseButton, // 

    /*--------------------------
		Methods
	--------------------------*/
    getPCMedia, // PC media css code
    getTabletMedia, //  media css code
    getPhoneMedia, //  media css code
    getSmallFont, // 12 (12px)
    getMultLineEllipsis, // ...css
};

export default GlobalStyle;
